@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body{margin:0;padding:0;line-height:1;font-family:"roboto", sans-serif;color:#202020;background-color:#fafafa}.header{border-bottom:solid 1px #ca2100;background-color:#db4c3f;box-shadow:0 1px 2px rgba(0,0,0,0.15);height:44px;width:100%}.header img{width:40px}.header nav{height:100%;max-width:922px;display:flex;align-items:center;margin:auto}.content{max-width:922px;display:flex;margin:auto}.sidebar{-webkit-user-select:none;-ms-user-select:none;user-select:none;width:266px;height:100vh;padding-top:74px;border-right:1px solid #f1f1f1;background-color:#fafafa}.sidebar div{margin-bottom:10px;cursor:pointer;padding:15px}.sidebar div.active{background-color:white}.sidebar div .icon{margin-right:10px}.tasks{background-color:white;padding:74px 28px 0;height:100vh;width:calc(922px - 266px)}.tasks .task-items-container{margin-top:40px;padding:0 20px}.tasks .task-items-container .task-item{display:flex;justify-content:space-between}.tasks .task-items-container .task-item:not(:last-child){border-bottom:1px solid #db4c3f}.tasks .add-task-btn{cursor:pointer}.tasks .add-task-btn .plus{color:#dd4b39;font-size:18px}.tasks .add-task-btn .add-task-text{color:#545454;font-size:14px;margin-left:15px}.tasks .add-task-dialog{margin-top:10px}.tasks .add-task-dialog input{background-color:white;width:100%;height:35px;border:1px solid #ddd;border-radius:3px;padding-right:10px;padding-right:10px}.tasks .add-task-dialog .add-task-actions-container{margin-top:10px;display:flex;justify-content:space-between}.tasks .btns-container{margin-top:10px}.tasks .btns-container .add-btn{background-color:#dd4b39;color:white;font-size:12px;border:1px solid white;cursor:pointer;padding:5px 10px;border-radius:3px}.tasks .btns-container .add-btn:disabled{background-color:gray}.tasks .btns-container .add-btn:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.tasks .btns-container .cancel-btn{all:unset;color:#555;cursor:pointer;font-size:14px;margin:0px 5px}

